import React, { Component } from 'react'
import Img from 'gatsby-image'
import PropTypes from 'prop-types';

import Preview from 'components/Landing/Preview'

import { H3 } from '@style/global'
import styled from 'styled-components'
import theme from '@style/theme'

import Popup from 'reactjs-popup'
import { IconClose } from 'components/Icon/IconClose'
import ScrollAnimation from 'react-animate-on-scroll'

//https://ibaslogic.com/instant-post-switching-in-a-gatsbyjs-site/

const getCategories = items => {
  let categoryItems = items.map(item => {
    return item.post.frontmatter.category
  })
  let flatCategories = categoryItems.reduce(
    (categoryItems, val) => categoryItems.concat(val),
    []
  )
  let uniqueCategories = new Set(flatCategories)
  let categories = Array.from(uniqueCategories)
  categories = ['All', ...categories]
  return categories
}

const PopUpPrev = ({children, item}) =>  (
  <StyledPopup
      trigger={
        <div>
          {children}
        </div>
      }
      modal
      closeOnDocumentClick
    >
    {close => (
      <div>
        <Close onClick={close}>
          <IconClose/>
        </Close>
          <Preview
            title={item.post.frontmatter.title}
            desc={item.post.frontmatter.description}
            date={item.post.frontmatter.date}
            img={
              item.post.frontmatter.image.childImageSharp.fluid
            }
            role={item.post.frontmatter.role}
            category={item.post.frontmatter.category}
            path={item.post.frontmatter.path}
            visit={item.post.frontmatter.visit}
            hideStory={item.post.frontmatter.hideStory}
          />
      </div>
    )}
  </StyledPopup>
);

PopUpPrev.propTypes = {
children: PropTypes.element.isRequired,
};

class ProjectItems extends Component {
  state = {
    items: this.props.items.remark.posts,
    blogPostItems: this.props.items.remark.posts,
    categories: getCategories(this.props.items.remark.posts),
  }
  handleItems = category => {
    if (category === 'All') {
      this.setState({
        blogPostItems: [...this.state.items],
      })
    } else {
      this.setState({
        blogPostItems: [
          ...this.state.items.filter(item => {
            return item.post.frontmatter.category.includes(category)
          }),
        ],
      })
    }
  }
  render() {
    return (
      <>
        {/* FILTER OF PROJECT CATEGORIES */}
        <div>
          {this.state.categories.map((category, index) => {
            return (
              <BtnCategory>
                <button
                  type="button"
                  key={index}
                  onClick={() => this.handleItems(category)}
                >
                  {category}
                </button>
              </BtnCategory>
            )
          })}
        </div>

        {/* GRID OF PROJECTS */}
        <ProjectGrid>
          {this.state.blogPostItems.map(item => {
            const gridStyle = {
              gridArea: `${item.post.frontmatter.grid}`,
            }

            const listCategory = item.post.frontmatter.category.map(
              (item, i) => {
                return <ListCategory key={i}>{item}</ListCategory>
              }
            )
            return (
              <ProjCard key={item.post.frontmatter.path} style={gridStyle} infoShow={item.post.frontmatter.infoShow}>
                {item.post.frontmatter.infoShow ? (
                  ''
                ) : (
                  <InfoWrapper>
                    <PopUpPrev item={item}>
                      <Image infoShow={item.post.frontmatter.infoShow}>
                        <ScrollAnimation
                          animateIn="fadeIn"
                          offset={0}
                          animateOnce="true"
                        >
                          <Img fluid={ item.post.frontmatter.image.childImageSharp.fluid}/>
                        </ScrollAnimation>
                      </Image>
                    </PopUpPrev>
                    <CategoryDate>
                      {listCategory}
                      <time dateTime={item.post.frontmatter.date}>
                        {item.post.frontmatter.date}
                      </time>
                    </CategoryDate>
                    <PopUpPrev item={item}>
                      <H3>{item.post.frontmatter.title}</H3>
                    </PopUpPrev>
                    <Brief>{item.post.frontmatter.brief}</Brief>
                  </InfoWrapper>
                )}
                {item.post.frontmatter.infoShow ? (
                  <Quote>
                    <H3>{item.post.frontmatter.quote}</H3>
                  </Quote>
                ) : (
                  ''
                )}
              </ProjCard>
            )
          })}
        </ProjectGrid>
      </>
    )
  }
}

export default ProjectItems

const ProjectGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 30px;
  margin: 68px 0 72px 0;

  @media (max-width: ${theme.screen.lg}) {
    width: 100%;
    justify-content: start;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    div {
      grid-area: auto / auto / auto / auto !important;
    }
  }

  @media (max-width: ${theme.screen.sm}) {
    grid-gap: 24px;
    grid-template-areas: auto / auto / auto / auto;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
`

