import React, { Component } from 'react'
import { Link } from 'gatsby'
import Img from 'gatsby-image'

import { H3, Button } from '@style/global'
import styled from 'styled-components'
import theme from '@style/theme'
import { IconExternal } from 'components/Icon/IconExternal'

class Preview extends Component {
  render() {
    const listRole = this.props.role.map((item, i) => {
      return <LsRole key={i}>{item}</LsRole>
    })
    return (
        <PreviewBloc>
            <ImgSlide>
              {this.props.img ? <Img fluid={this.props.img} /> : ''}
            </ImgSlide>
            <Info>
              <H3>{this.props.title}</H3>
              <Desc>{this.props.desc}</Desc>
              <p>
                <TitleRole>Roles</TitleRole>
                {listRole}
              </p>
              <BtnGroup>
                {this.props.visit ? (
                  <Link to={this.props.visit} target="blank">
                    <Button>Visit Work <IconExternal/></Button>
                  </Link>
                ) : (
                  ''
                )}
                {this.props.hideStory ? (
                  ''
                ) : (
                  <Link to={this.props.path}>
                    <Button Tertiary>Read Story</Button>
                  </Link>
                )}
              </BtnGroup>
            </Info>
        </PreviewBloc>
    )
  }
}

export default Preview

const PreviewBloc = styled.div`
  display: grid;
  height: 100% !important;
  grid-template-columns: 4fr 2fr;
  padding: 0 !important;
  margin: 0 !important;
  background: ${theme.color.primary.S200D}; 
  border: none !important;
  @media (max-width: ${theme.screen.lg}) {
    grid-template-columns: unset !important;
    height: auto !important;
  }
`

const Info = styled.div`
  padding: 40px 100px 50px 20px;
  height: 100% !important;
  color: ${theme.color.primary.S1000D};
  @media (max-width: ${theme.screen.lg}) {
    padding: 25px 16px 0 16px;
  }

  @media (min-width: ${theme.screen.xl}) {
    padding: 80px 140px 0 40px;
  }
  ${H3} {
    font-weight: bold;
    font-size:1.25em !important;
    line-height: 1.72em !important;
    @media (min-width: ${theme.screen.lg}) { 
      margin-bottom: 5px;
    }
  }
`

const ImgSlide = styled.div`
  @media (max-width: ${theme.screen.lg}) {
    order: -1;
    height: 200px;
    object-fit: cover;
  }
  div {
    height: 100% !important;
  }
  img {
    object-fit: cover;
    object-position: 50% 50%;
  }
`

const Desc = styled.p`
  @media (max-width: ${theme.screen.lg}) {
    ${theme.fontSize.sm};
  }
`
const TitleRole = styled.div`
  font-family: ${theme.font.primary};
  ${theme.fontSize.xs};
  font-weight: bold;
  margin: 5px 0;
`
const LsRole = styled.span`
  ${theme.fontSize.sm};
  ::after {
    content: ' / ';
  }
  :last-of-type::after {
    content: '';
  }
`

const BtnGroup = styled.div`
  margin: 35px 0;
  @media (max-width: ${theme.screen.lg}) {
    margin: 25px 0;
  }
  div {
    margin-left: 10px;
  }
  div:first-of-type {
    margin-left: 0px !important;
  }
`
